/*wBreadcrumbs.scss*/
@import "../_config";
@import '../../MediaFiles/Fonts/fonts';
.breadcrumbs {
    width: 100%;
    display: inline-block;
    vertical-align: middle;
    font-size: 0;
    line-height: 0;
    > span {
        display: inline-block;
        vertical-align: top;
        font-size: 14px;
        line-height: 18px;
        margin-right: 7px;
        color: #6d6d6d;
        text-transform: uppercase;
        font-size: 11px;
        line-height: 25px;
        font-family: 'OpenSans', Arial, Helvetica, sans-serif;
        &:before {
            content: '/';
            display: inline-block;
            vertical-align: top;
            margin-right: 7px;
        }
        &:first-child:before {
            display: none;
        }
        > a {
            text-decoration: none;
            color: #282828;
            text-transform: uppercase;
            font-size: 11px;
            line-height: 25px;
            font-family: 'OpenSans', Arial, Helvetica, sans-serif;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
            &:hover {
                color: #ed1c24;
            }
        }
    }
}
@media only screen and (max-width:600px) {
    .breadcrumbs {
        > span {
            display: none;
            &:before {
                display: none;
            }
            &:nth-last-child(2) {
                display: block;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                max-width: 100%;
                background: none;
                margin: 0;
                &:before {
                    content: '<';
                    display: inline-block;
                    margin: 0 5px 0 3px;
                }
            }
        }
    }
}