/*wBtn.scss*/
@import "../_config";
@import '../../MediaFiles/Fonts/fonts';
$lineHeight: $emSize * 2 + $unit * 2;

$boxShadowBtn: inset 0 (-$unit * 2) $unit * 3 rgba($colorWhite, .45), inset 0 $unit * 2 $unit * 3 rgba($colorBlack, .35), 0 $unit * 2 0;

$height: $lineHeight - $unit;
$padding: $emSize + $unit;

$svgHeight: $emSize + $unit * 1.5;
$svgWidth: $svgHeight;
$svgMargin: -$unit $emSizeHalf - $unit 0 0;

$borderRadius: $emSize * 0.4;

.wBtn {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: auto;
    font-size: 16px;
    height: 36px;
    line-height: 34px;
    border: 1px solid #fff;
    background: none;
    box-sizing: border-box;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    padding: 0 15px;
    cursor: pointer;
    font-family: 'OpenSans', Arial, Helvetica, sans-serif;
    color: #fff;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    -ms-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    > .svgHolder {
        margin: $svgMargin;
        text-decoration: none !important;
        display: inline-block;
        vertical-align: middle;
        height: $svgHeight;
        width: $svgWidth;
        > svg {fill: $colorMiddleDark; }
    }
    &.w_yellow {
        background: #ffc107;
        border: none;
        border-bottom: 1px solid #df9821;
        height: 46px;
        box-sizing: border-box;
        padding: 0;
        line-height: 45px;
        > .svgHolder {
            text-decoration: none !important;
            display: inline-block;
            vertical-align: top;
            height: 45px;
            width: 45px;
            box-sizing: border-box;
            border: none;
            border-right: 1px solid #df9821;
            line-height: 45px;
            margin: 0;
            > svg {
                fill: #282828;
                width: 25px;
                height: 25px;
                margin: 10px;
            }
        }
        span {
            padding: 0 15px;
            display: inline-block;
            vertical-align: top;
            font: 16px/45px 'OpenSans', Arial, Helvetica, sans-serif;
            color: #282828;
            box-sizing: border-box;
            font-weight: bold;
        }
        &:hover {
            background: #df9821;
            > .svgHolder {
                border-right: 1px solid transparent;
            }
        }
        &:active {
            -webkit-transform: translate(0,2px);
            transform: translate(0,2px);
        }
    }
    &.w_blue {
        background: #3db8af;
        border: none;
        border-bottom: 1px solid #297e78;
        height: 46px;
        box-sizing: border-box;
        padding: 0 50px;
        line-height: 45px;
        span {
            padding: 0;
            display: inline-block;
            vertical-align: top;
            font: 17px/45px 'OpenSans', Arial, Helvetica, sans-serif;
            color: #fff;
            box-sizing: border-box;
            font-weight: bold;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease;
        }
        &:hover {
            background: #297e78;
            span {
                color: #fff;
            }
        }
        &:active {
            -webkit-transform: translate(0,2px);
            transform: translate(0,2px);
        }
    }
    &.w_transparent {
        background: none;
        border: none;
        border-bottom: 1px solid transparent;
        height: 46px;
        box-sizing: border-box;
        padding: 0;
        line-height: 45px;
        > .svgHolder {
            text-decoration: none !important;
            display: inline-block;
            vertical-align: top;
            height: 45px;
            width: 45px;
            box-sizing: border-box;
            border: none;
            border-right: 1px solid transparent;
            line-height: 45px;
            margin: 0;
            > svg {
                fill: #282828;
                width: 25px;
                height: 25px;
                margin: 10px;
            }
        }
        span {
            padding: 0 10px 0 0;
            display: inline-block;
            vertical-align: top;
            font: 14px/45px 'OpenSans', Arial, Helvetica, sans-serif;
            color: #282828;
            box-sizing: border-box;
            font-weight: bold;
        }
        &:active {
            -webkit-transform: translate(0,2px);
            transform: translate(0,2px);
        }
    }
    &:hover {
        color: #000;
        background: #fff;
    }
}